@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
:root {
  --poppins: "Roboto", sans-serif;
}

:root {
  --bg-color: #000;
  --main-color: #660104;
  --text-color: #333;
  --second-text-color: #555;
  --white-color: #fff;
  --cover-color: linear-gradient(45deg, #980206, #330102);
  --pages-color: linear-gradient(90deg, #cbb26a, #d8c690);
  --border: rem(2) solid #be9e44;
  --box-shadow: 0 0 rem(9.6) rgba(0, 0, 0, 0.2);
}

html {
  font-size: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  outline: none;
  font-family: var(--poppins);
}

.exterior {
  position: relative;
  width: 66rem;
  height: 44rem;
  padding: 2rem;
  perspective: 250rem;
  animation: display-animation 2s forwards;
}
.exterior__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background-color: var(--cover-color);
  box-shadow: var(--box-shadow);
  border-top-right-radius: 0.6rem;
  border-bottom-left-radius: 0.6rem;
  transform-origin: right;
}
.exterior__cover__cover-left {
  z-index: -1;
}
.exterior__cover__cover-right {
  z-index: 100;
  transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
}