:root {
    --bg-color: #000;
    --main-color: #660104;
    --text-color: #333;
    --second-text-color: #555;
    --white-color: #fff;
    --cover-color: linear-gradient(45deg, #980206, #330102);
    --pages-color: linear-gradient(90deg, #cbb26a, #d8c690);
    --border: rem(2) solid #be9e44;
    --box-shadow: 0 0 rem(9.6) rgba(0, 0, 0, 0.2);
}
